import React from 'react';
import '../App';
import { useEffect } from "react";
import { Link } from 'react-router-dom';

const About = () => {
  useEffect(() => {
    document.body.classList.add("nav-text-black");
    return () => {
      document.body.classList.remove("nav-text-black");
    };
  }, []);
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <div class="page-wrapper">
      <section className='main-section pt-5 mt-md-0 aboutus' style={{ backgroundImage: "url(./image/about-us-banner.png)" }}>
        <div className='slider-item'>
          <div className='items-slider-wrap'>
            <div className='row align-items-center'>
              <div className='col-md-7'>
                <div className='text-slider text-white'>
                  <h1>About Hone</h1>
                  <p>The team at Hone combines the latest advances in spectroscopy and machine learning for rapid measurement and actionable decision making.</p>
                </div>
              </div>
              <div className='col-md-5 ' data-aos="zoom-in-up" data-aos-duration="1000">
                <img src='./image/scratch-banner.png' alt='' className='img-fluid w-100 d-md-none d-block' />
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='section-products'>
        <div className='container'>
          <div className='row align-items-center'>
            <div className='col-md-6'>
              <div className='product-item-wrap'>
                <div className='text-card'>
                  <h2 className='section-heading-32'>The Hone Group delivers solutions in spectroscopy for a range of applications.</h2>
                  <p className='paragarph'>Hone provides measurement solutions for primary production, food and commodity processing, and for the improvement of soil health in improving carbon stores.</p>
                  <p className='paragarph'>Advances in machine learning provides Hone Create; a tool for building chemometric models and delivering results using these complex mathematical algorithms.</p>
                  <p className='paragarph'>The group strives to become a global leader in measurement solutions while improving efficiency for the benefit of our business partners, and our shared environment.</p>
                </div>
              </div>
            </div>
            <div className='col-md-6'>
              <div className='image-about-wrap' data-aos="zoom-in-up" data-aos-duration="1000">
                <img src='image/about-us-dummy.png' className='img-fluid w-100'></img>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='section-products py-3 py-md-5'>
        <div className='container'>
          <div className='row align-items-center'>
            <div className='col-md-6'>
              <div className='image-about-wrap' data-aos="zoom-in-up" data-aos-duration="1000">
                <img src='image/about-us-dummy.png' className='img-fluid w-100'></img>
              </div>
            </div>
            <div className='col-md-6'>
              <div className='product-item-wrap'>
                <div className='text-card'>
                  <h3 className='text-left mb-md-5 mb-3'>The Hone Group takes pleasure in forming relationships with business and research partners. Putting decision making in your hands, powered by our next generation spectrometers. Learn more about the Hone Lab for on-farm use.</h3>
                  <p className='paragarph'>Hone enjoy contributing and being active within various sectors, working with leading industry bodies and nationally-accredited research laboratories.</p>
                  <p className='paragarph'>Hone won a Wine Industry Suppliers Association Impact Award for Innovation in 2018 and two Good Design Australia Awards in 2021 with the Hone Lab Red.</p>
                  {/* <Link to='/contact-us' className='btn btn-primary mt-5'>Enquire now</Link> */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='our-technology'>
        <div className='container'>
          <div className='row'>
            <div className='col-md-6'>
              <h2 className='section-heading'>Test What You Want When You Want</h2>
            </div>
          </div>
          <div className='row'>
            <div className='col-md-4'>
              <div className='card text-center' data-aos="zoom-in-left" data-aos-duration="1000">
                <img src='./image/technology1.png' className='img-fluid w-100'></img>
                <div className='text-card'>
                  <h3 className='mb-20'>Our Story</h3>
                  <p className='paragarph'>Read more about the Hone’s vision and values.</p>
                  <Link to='/' className='learn-more'>Read more</Link>
                </div>
              </div>
            </div>
            <div className='col-md-4'>
              <div className='card text-center' data-aos="zoom-in-up" data-aos-duration="1000">
                <img src='image/technology2.png' className='img-fluid w-100'></img>
                <div className='text-card'>
                  <h3 className='mb-20'>Our Team</h3>
                  <p className='paragarph'>Meat the team delivering knowledge now.</p>
                  <Link to='/our-team' className='learn-more'>Read more</Link>
                </div>
              </div>
            </div>
            <div className='col-md-4'>
              <div className='card text-center' data-aos="zoom-in-right" data-aos-duration="1000">
                <img src='image/technology3.png' className='img-fluid w-100'></img>
                <div className='text-card'>
                  <h3 className='mb-20'>Read the blog</h3>
                  <p className='paragarph'>Learn more about Hone’s research and applications.</p>
                  <Link to='/blog-page' className='learn-more'>Read more</Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );

}

export default About;
