import React, { useEffect } from "react";

const About = () => {
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <div className='page-wrapper mt-80'>
      <section className='document-link'>
        <div className='container'>
          <div className='row'>
            <div className='col-md-12'>
              <div className='mb-5'>
                <h2 className="section-heading">Documents</h2>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-4">
              <h3 className="font-weight-bold">Terms</h3>
              <ul className="link-documents">
                <li>
                  <a href="https://www.honeassets.com/files/hone-terms.pdf?c=Release-689">Hone Terms <i class="fa fa-arrow-right" aria-hidden="true"></i></a>
                </li>
                <li>
                  <a href="https://www.honeassets.com/files/hone-create-terms.pdf?c=Release-689">Hone Create Terms <i class="fa fa-arrow-right" aria-hidden="true"></i></a>
                </li>
                <li>
                  <a href="https://www.honeassets.com/files/third-party-software.pdf?c=Release-689">Third-party Software <i class="fa fa-arrow-right" aria-hidden="true"></i></a>
                </li>
              </ul>
            </div>
            <div className="col-md-4">
              <h3 className="font-weight-bold">Privacy</h3>
              <ul className="link-documents">
                <li>
                  <a href="https://www.honeassets.com/files/hone-privacy-policy.pdf?c=Release-689">Hone Privacy Policy <i class="fa fa-arrow-right" aria-hidden="true"></i></a>
                </li>
              </ul>
            </div>
            <div className="col-md-4">
              <h3 className="font-weight-bold">Agreements</h3>
              <ul className="link-documents">
                <li>
                  <a href="https://www.honeassets.com/files/hone-model-license-agreement.pdf?c=Release-689">Hone Model License Agreement <i class="fa fa-arrow-right" aria-hidden="true"></i></a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
    </div>
  );

}

export default About;
